@import "../../../global.scss";

.slider {
    position: relative;

    .icons {
        position: absolute;
        top: 40%;
        left: 4%;
        z-index: 5;
        display: flex;
        flex-direction: row;
        gap: 48rem;

        
        .icon1,
        .icon2 {
            align-items: flex-end;
            width: 2.5rem;
            height: 2.5rem;
            border-radius: 50%;
            background-color: rgba(88, 87, 87, 0.5);
            
            .i {
                color: beige;
                margin-left: .65rem;
                margin-top: .65rem;
            }

        }
    }

    &>* {
        font-family: $font;
    }


    img {
        position: relative;
        height: 25rem;
        width: 58rem;
        border-radius: 2rem;
    }

    .fflex {
        position: absolute;
        top: 0;
        display: flex;
        flex-direction: column;
        gap: 10rem;
        z-index: 3;

        .person {
            width: 20rem;
            margin-left: 5rem;
            margin-top: 2rem;
            display: flex;
            flex-direction: row;
            position: relative;

            img {
                border-radius: 50%;
                width: 2.3rem;
                height: 2rem;
            }

            .img2 {
                position: absolute;
                left: 10%;
            }

            .img3 {
                position: absolute;
                left: 19%;
            }

            h3 {
                letter-spacing: .1rem;
                font-size: .9rem;
                font-weight: 100;
                color: rgb(205, 205, 191);
                margin-top: 2.3%;
                margin-left: 23%;
            }
        }

        .info {
            margin-left: 5rem;

            h3 {
                color: rgb(145, 129, 129);
                margin-top: 1rem;
                font-weight: 100;

                span {
                    margin-left: 1rem;
                    color: rgb(205, 205, 191);
                    font-weight: 100;
                }
            }

            &>* {
                color: beige;
            }

            .down {
                margin-top: 1rem;
                display: flex;
                flex-direction: row;
                gap: 1rem;

                .btnw {
                    font-size: 1rem;
                    width: 8rem;
                    height: 2.5rem;
                    border: none;
                    border-radius: .5rem;
                    background-color: rgb(207, 23, 23);
                    color: beige;
                }

                .btnp {
                    font-size: 2rem;
                    background-color: rgba(88, 87, 87, 0.5);
                    color: beige;
                    width: 2.5rem;
                    height: 2.5rem;
                    border: none;
                    border-radius: .5rem;
                    // opacity: 0.7;
                }
            }
        }

    }
}